.home {
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
    
  }

  .home_row {
    display: flex;
    z-index: 1;
    margin: 0 5px;
  }
  .home_image{
    width:100%;
    object-fit:cover; 
    justify-content: center;
    overflow: hidden;
    z-index:-1;
    margin-bottom: -150px;
    mask-image: linear-gradient(to bottom, rgb(19, 17, 17) 50%, rgba(0, 0, 0, 0.5))
    

}